:root {
  --thumb-width: 150px;
  --thumb-max-height: 150px;
  --thumb-margin: 0 20px;
  --thumb-border-radius: 15px;
  --thumb-object-fit: contain;
  --thumb-text-align: center;
  --details-width: 700px;
  --details-overflow: visible;
  --main-width: 75%;
  --main-min-width: 768px;
  --main-margin: auto;
  --radio-side: 50px;
  --price-margin-left: auto;
  --row-display: flex;
  --row-align-items: flex-start;
}

html {
  font-size: 22px;
  /* font-family: 'Montserrat', sans-serif; */
}
body {
  /* font-family: 'Montserrat', sans-serif; */
}
/* This ensures that when an image, or radio button, is clicked, an outline does not appear over it.  */
:focus {
  outline: none;
}
.topbar {
  height: 50px;
  background-color: aliceblue;
}
.topbar__inner {
  width: var(--main-width);
  min-width: var(--main-min-width);
  margin: var(--main-margin);
  font-size: 0.75rem;
}
.wrapper {
  width: var(--main-width);
  min-width: var(--main-min-width);
  margin: var(--main-margin);
}
.header {
  border-radius: 15px;
  border: 5px solid #406c1c;
  margin: 20px 0;
  color: #333333;
  font-size: 5.5rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  text-align: center;
}
/* Display header version 2 with this (background image). */
