    height: 150px;
    background-image: url("images/header-1-1428-b.jpg");
    border-radius: 15px;
    margin-top: 20px;
    margin-bottom: 20px;    
} */
.header__desc {
  font-size: 0.9rem;
  padding: 0 150px 10px 150px;
  line-height: 30px;
}
.filter {
  height: 350px;
  display: flex;
  justify-content: space-around;
  border: 1px solid #f4eff1;
}

/* Begin radio button style. */
.radio {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-size: 1.8rem;
}
.radio__img {
  width: 200px;
  border-radius: 15px;
}
.radio__input {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: 4px solid #ccc;
  border-top-color: #bbb;
  border-left-color: #bbb;
  background: #fff;
  width: var(--radio-side);
  height: var(--radio-side);
  border-radius: 50%;
}
.radio__input:checked {
  border: 20px solid #4099ff;
}
.heartBeat {
  -webkit-animation-name: heartBeat;
  animation-name: heartBeat;
  -webkit-animation-duration: 1.3s;
  animation-duration: 1.3s;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
}
@keyframes heartBeat {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  14% {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }
  28% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  42% {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }
  70% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@-webkit-keyframes heartBeat {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  14% {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }
  28% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  42% {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }
  70% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
/* End radio button style. */

.tabhead {
  display: var(--row-display);
  align-items: var(--row-align-items);
  margin-top: 60px;
  font-size: 0.75rem;
  font-weight: 600;
}
.tabhead__thumb {
  width: var(--thumb-width);
  max-height: var(--thumb-max-height);
  margin: var(--thumb-margin);
  border-radius: var(--thumb-border-radius);
  object-fit: var(--thumb-object-fit);
  text-align: var(--thumb-text-align);
}
.tabhead__details {
  width: var(--details-width);
  overflow: var(--details-overflow);
}
.tabhead__info {
  width: 275px;
  text-align: center;
}
.tabhead__price {
  margin-left: var(--price-margin-left);
}
.row {
  display: var(--row-display);
  align-items: var(--row-align-items);
  padding: 30px 0;
  border-bottom: 1px solid #f4eff1;
  border-top: 1px solid #f4eff1;
  min-height: 150px;
}
.row__thumb {
  width: var(--thumb-width);
  max-height: var(--thumb-max-height);
  margin: var(--thumb-margin);
  border-radius: var(--thumb-border-radius);
  object-fit: var(--thumb-object-fit);
  text-align: var(--thumb-text-align);
}
.row__name {
  margin-top: 10px;
  font-size: 0.9rem;
}
.row__desc {
  font-size: 0.7rem;
}
.row__disc,
.row__ss {
  font-size: 0.55rem;
  font-style: italic;
}
.row__disc {
  margin-top: 30px;
}
.row__ss {
  margin-top: 10px;
}
.row__details {
  width: var(--details-width);
  overflow: var(--details-overflow);
}
.row__dates {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.row__datetext {
  font-size: 0.7rem;
}
.row__storinfo {
  display: flex;
  justify-content: space-around;
}
.row__storlogo {
  height: 40px;
  padding-right: 10px;
}
.row__storaddress {
  font-size: 0.5rem;
}
.row__dateprefix {
  font-size: 0.5rem;
  margin-right: 5px;
}
.row__price {
  align-self: flex-end;
  margin-left: var(--price-margin-left);
  font-size: 1.13rem;
}
.fade-appear {
  opacity: 0;
  z-index: 1;
}
.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 1300ms linear;
}
